function MyFirebaseSignInButton() {

  const navigate = useNavigate();

  if (getAuth().currentUser) {
    return <StyledFirebaseAuth uiConfig={uiConfig} firebaseAuth={getAuth()} />
  } else {
    
  return <div style={{textAlign:"center"}}>
  <Button type="primary" size="large" onClick={()=> {getAuth().signOut().then(()=> navigate("#"))   }}>sign Out</Button>
  </div>
  }
}

export default MyFirebaseSignInButton;